import React, { Component } from 'react';
import { Logger, LoggingConfig } from '@chr/common-javascript-logging-core'
import { LoggingErrorBoundary } from '@chr/common-javascript-logging-error-boundary'

export interface LoggingWrapperProps {
  loggingConfig: LoggingConfig
  render?: LoggingErrorBoundary['props']['render']
}

export class LoggingWrapper extends Component<LoggingWrapperProps> {
  constructor(props) {
    super(props);

    Logger.configure(props.loggingConfig)
  }

  render() {
    return (
      <LoggingErrorBoundary {...this.props} />
    );
  }
}
