import { Component, KeyboardEventHandler, createRef } from 'react';
import { MouseEventHandler } from 'react';
import { ButtonProps } from '../buttons/_button'

export interface ModalProps {
  title?: string
  headerClassNames?: string
  children?: any
  closeAction?: MouseEventHandler<HTMLButtonElement|HTMLDivElement> | KeyboardEventHandler<Document>
  primaryButtonProps?: ButtonProps
  secondaryButtonProps?: ButtonProps
  keepOpenOnOverlay?: boolean

  /** @deprecated Please use `secondaryButtonProps` instead */
  secondaryActionLabel?: string
  /** @deprecated Please use `secondaryButtonProps` instead */
  secondaryAction?: MouseEventHandler<HTMLButtonElement>
  /** @deprecated Please use `primaryButtonProps` instead */
  primaryActionLabel?: string
  /** @deprecated Please use `primaryButtonProps` instead */
  primaryAction?: MouseEventHandler<HTMLButtonElement>
}

export interface ModalState {
  fadeTransition: string
}

export class ChrModal extends Component<ModalProps, ModalState> {
  state = {
    fadeTransition: ''
  }
  private modalRef = createRef<HTMLDivElement>();

  componentDidMount() {
    this.setState({
      fadeTransition: 'in'
    });
    document.addEventListener('keyup', this.keyupCallback);
    document.body.classList.add('modal-open');
  }

  componentWillUnmount() {
    document.removeEventListener('keyup', this.keyupCallback);
    document.body.classList.remove('modal-open');
  }

  private keyupCallback = (e) => {
    const isEscapeKey = e.key && e.key.substring(0, 3).toLowerCase() === 'esc';
    if (this.props.closeAction && isEscapeKey) {
      (this.props.closeAction as KeyboardEventHandler<Document>)(e)
    }
  }

  private onModalOverlayClick = (e: React.MouseEvent<HTMLDivElement>) => {
    if (e.target === this.modalRef.current && this.props.closeAction && !this.props.keepOpenOnOverlay) {
      (this.props.closeAction as MouseEventHandler<HTMLDivElement>)(e)
    }
  }

  private getButtonPropsFromActionProps(action?: MouseEventHandler<HTMLButtonElement>, label?: string) {
    // Angular ensures that an implementation of an output binding is always provided,
    // so we must check both the action and the label.
    const deprecatedModalPropsDefined: boolean = typeof (action) !== 'undefined' && !!label
    return !deprecatedModalPropsDefined ? null
      : {
        buttonClicked: action,
        text: label,
        disabled: action === null
      } as ButtonProps
  }

  /**
   * If set, gets ButtonProps from 'props.primaryButtonProps'
   * If not set, and the deprecated 'props.primaryAction' and 'props.primaryActionLabel'
   * are set, creates and returns new ButtonProps from these.
  */
  private get primaryButtonProps(): ButtonProps | null {
    return this.props.primaryButtonProps || this.getButtonPropsFromActionProps(this.props.primaryAction, this.props.primaryActionLabel)
  }

  /**
   * If set, gets ButtonProps from 'props.secondaryButtonProps'
   * If not set, and the deprecated 'props.secondaryAction' and 'props.secondaryActionLabel'
   * are set, creates and returns new ButtonProps from these.
  */
  private get secondaryButtonProps(): ButtonProps | null {
    return this.props.secondaryButtonProps || this.getButtonPropsFromActionProps(this.props.secondaryAction, this.props.secondaryActionLabel)
  }

  render() {
    return (
      <div>
        <div
          className='modal-backdrop fade in'
          style={{ display: 'block' }} />
        <div
          className={`modal fade ${this.state.fadeTransition}`}
          role='dialog'
          aria-labelledby='modal'
          style={{ display: 'block' }}
          onClick={this.onModalOverlayClick}
          ref={this.modalRef}
        >
          <div className='modal-dialog' role='document'>
            <div className='modal-content'>
              <div className='modal-header'>
                {this.props.closeAction ?
                  <button
                    type='button'
                    className='close'
                    aria-label='Close'
                    onClick={this.props.closeAction as MouseEventHandler<HTMLButtonElement>}>
                    <span aria-hidden='true' className='ns-icon ns-md ns-close ns-solo' />
                  </button>
                  : null}
                <h4 className={`modal-title ${this.props.headerClassNames}`} id='myModalTitle'>
                  {this.props.title}
                </h4>
              </div>
              <div className='modal-body' style={{ maxHeight: 'calc(100vh - 180px)', overflowY: 'auto' }}>
                {this.props.children}
              </div>
              <div className='modal-footer'>
                {this.secondaryButtonProps ?
                  <button
                    type='button'
                    className='btn btn-default'
                    onClick={this.secondaryButtonProps.buttonClicked}
                    disabled={this.secondaryButtonProps.disabled}>
                    {this.secondaryButtonProps.text}
                  </button>
                  : null}
                {this.primaryButtonProps ?
                  <button
                    type='button'
                    className='btn btn-primary'
                    onClick={this.primaryButtonProps.buttonClicked}
                    disabled={this.primaryButtonProps.disabled}>
                    {this.primaryButtonProps.text}
                  </button>
                  : null}
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
